import React from 'react';
import './Nav.css';

const nav = (props) => {

  return (
    <div>
    <header>
          <ul className={props.section}>
            <li className='header-buttons'><a id="about" href='#aboutMe-section'>About</a></li>
            <li className="header-buttons"><a id="projects"href="#projects-section">Projects</a></li>
          </ul>
      </header>

    </div>

);
  }


export default nav;