import React, { Component } from 'react';
import './App.css';

import Nav from '../Nav/Nav';

import Profile from '../assets/images/profile.png';
import Twitter from '../assets/images/twitter.png';
import Linkedin from '../assets/images/linkedin.png';
import Github from '../assets/images/github.png';
import Mail from '../assets/images/mail.png';

import Jamming from '../assets/images/jamming.png';
import Matching from '../assets/images/matching game.png';


import {Row, Col} from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import Particles from 'react-particles-js';


class App extends Component {
state = {
  salute : {
    spa : 'hola',
    eng : 'hello'
  },
  lenguage : {
    spa : 'spanish',
    eng : 'english'
  },
  info : {
    email: 'jsubcab@gmail.com'
  },
  page : 'about',
  number : 'page-1'

}
  salute = (whichLenguage) => {
    let sayHello = '';
    if (whichLenguage === 'spanish') {
      sayHello = this.state.salute.spa + '!';
    } else {
      sayHello = this.state.salute.eng + '!';
    }

    return sayHello;
  }

  handleClickStudies() {
    const works = document.querySelector('.works');
    const studies = document.querySelector('.studies');

        studies.classList.add("active");
        document.querySelector('.infoWork').style = 'display:none';
        document.querySelector('.infoStudies').style = 'display:block';

        if (works.classList.contains("active")) {
          works.classList.remove("active");
      
        }

  }

  handleClickWork() {
    const works = document.querySelector('.works');
    const studies = document.querySelector('.studies');

      works.classList.add("active");
      document.querySelector('.infoWork').style = 'display:block';
      document.querySelector('.infoStudies').style = 'display:none';
      studies.classList.remove("studies-default");

      if (studies.classList.contains("active")) {
        studies.classList.remove("active");
    
      }
  }


  render() {
    let lenguage = this.state.lenguage.spa;
    
    const listStudies=[
      'Computer Science Degree - Universitat Oberta de Catalunya',
       'Management Computer Systems College - IES Nicolau Copernic',
       'Front-End Web Developer Nanodegree - Udacity',
       'Build Front-End Web from Scratch - Codecademy'
      ];

    const listJobs=[
      'Internship Software Engineer - Nomadizers @ Barcelona - Spain',
        'Marketing & Sales LATAM - IQ Option @ Limassol - Cyprus, St. Petersburg - Russia',
        'Data Analyst - Sayos & Carrera @ Madrid - Spain',
        'Web Designer, Graphic Designer and Co-Organizer - Fiberparty @ Barcelona - Spain'
      ];

      
  var prevScrollpos = window.pageYOffset;
  window.onscroll = function() {
  var currentScrollPos = window.pageYOffset;
    if (prevScrollpos > currentScrollPos) {
      document.querySelector('header').style.top = "0";
    } else {
      document.querySelector('header').style.top = "-50px";
  
    }
    prevScrollpos = currentScrollPos;
  }

  /* JS scrolling shows button to top */
  window.onscroll = function() {scrollFunction()};

  function scrollFunction() {
    if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
      document.querySelector(".arrowBack").style.top = "0px";
    } else {
      document.querySelector(".arrowBack").style.top = "-100px";
    }
  }

  function topFunction() {
    document.body.scrollTop = 0; // For Safari
    document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
  }

  /* redirection cards */

  return (
    <div className='main-content'>
      <Nav section={this.state.page} />
      
      <div onClick={topFunction} className='arrowBack'>
       <i class="fas fa-chevron-up"></i>
      </div>

        <section className='section-about-1'>
          <h2>{this.salute(lenguage)}</h2>
        </section>

        <section id='aboutMe-section' className='section-about-2'>
        
            <Row className='row-section'>
            <Particles id="tsparticles" params={{
              background: {
                color: {
                  value: "#212121",
                },
                size:'cover'
              },
              fpsLimit: 60,

              }}
              style={{position:'absolute'}}/>
              
              <Col md={6} className='col-section'>
                <div className='col-information'>
                  <p>I am </p>
      
                  <h3>Jorge Cabrera</h3>
                
                  <h5 style={{marginBottom:"5%"}}>Software Engineer and Data Analyst</h5>
                  
                  <div className='col-information-main-text'>
                    <span>
                      I believe the perfect balance between this two worlds can
                      create a link with anyone, no matter the lenguage or the culture.
                    </span>
                      <hr className='hr-border' />
                    <span>
                      How to find the way is where the fun starts.
                    </span>
                  </div>
                 </div>
              </Col>
              <Col md={6}>
                <Row style={{height: "100%"}}>
                  <Col sm={12}>
                    <div className='col-goDown'>
                      <a href='#infoTitleUser'>
                        <i className="fas fa-chevron-down"></i>
                      </a>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          
        </section>

        <section className='section-about-3'>
            <Row id='row-aboutMe'>
              <Col md={4} id='infoTitle-Col'>
                  <div className='imageBase'>
                    <div className='imageBase-content'>
                      <img className='imageProfile' src={Profile} alt='profile-pic' />
                      <h5>Let's connect!</h5>
                      <div className='imageIcons'>
                        <a rel="noopener noreferrer" target="_blank" href='https://twitter.com/el_Borsho'><img src={Twitter} alt='twitter'></img></a>
                        <a rel="noopener noreferrer" target="_blank" href='https://www.linkedin.com/in/jsubcab/'><img src={Linkedin} alt='linkedin'></img></a>
                        <a rel="noopener noreferrer" target="_blank" href='https://github.com/jsubcab'><img src={Github} alt='github'></img></a>
                        <a href='mailto:jsubcab@gmail.com'><img src={Mail} alt='email'></img></a>
                    
                      </div>
                      <div className='imageButton' style={{display:'none'}}>
                        <a className='buttonCV' href="#"><span>View CV</span></a>
                      </div>
                    </div>
                  </div>
              </Col>
              <Col md={8}>
                <Row style={{height:'20%'}} id='infoTitle-row'>
                <Col sm={12}>
                  <div id='infoTitleUser' className='infoTitle'>
                    <h5>Hi there, I'm <span>Jorge Subiabre Cabrera</span></h5>
                  </div>
                </Col>
                </Row>
                <Row style={{height:'80%'}}>
                <Col sm={6} >
                  <div className='infoDescription'>
                    <span>
                      I'm a <span className='infoDescription-remark'>Software Engineer</span> from Barcelona, Spain, always intrigued by
                      the world of <span className='infoDescription-remark'>Data Analyst</span>.
                      <hr className='hr-border'></hr>
                      <div className='more-phrases'>
                      Throughout my career I always have found exciting how
                      to present a product with a functional and simple design no matter
                      how complex can be.
                      </div>
                      <hr className='hr-border'></hr>
                      That's why I decided to focus into <span className='infoDescription-remark'>Javascript, React, Java, Oracle</span> and
                      also <span className='infoDescription-remark'>Adobe CC</span>.
                      <hr className='hr-border'></hr>
                      I think the mix between those can make anything possible and I love it!
                    </span>
                  </div>
                </Col>
                <Col sm={6}>
                  <div className='infoDescription2'>
                    <span><span className='infoDescription-remark'>No more introductions!</span> Here is a resume of what I have been doing:</span>
                  
                    <Row style={{marginTop:"20px"}}>
                      <Col sm={12}>
                        <button className='buttons studies studies-default' onClick={this.handleClickStudies}>Academic</button>
                        <button className='buttons works' onClick={this.handleClickWork}>Jobs</button>
                      
                      </Col>
                    </Row>
                    <Row>
                      <Col style={{marginTop:"10px"}} sm={12}>
                        <ul className='ul-info'>
                          <div className='infoStudies'>
                            {    listStudies.map((item, i) =>
                                {
                                  return (
                                  <li key={i} className='listInfo'>{item}</li>
                                  );
                                })
                              }
                          </div>
                          <div className='infoWork'>
                          {    listJobs.map((item, i) =>
                                {
                                  return (
                                  <li key={i} className='listInfo'>{item}</li>
                                  );
                                })
                              }
                          </div>
                        </ul>
                      </Col>
                    </Row>
                  </div>
                </Col>
                </Row>
              </Col>
            </Row>
        </section>

        <section id ="projects-section" className='section-about-4'>
              <Row style={{height:"100%;"}}>
                <Col sm={12} md={6} className='list-columns'>
                  <div className='list-projects'>
                      <Card >
                        <Card.Img variant="top" src={Matching} />
                        <Card.Footer>
                            <p className='projects-section'>COURSE PROJECT</p>
                            <Card.Title><a rel="noopener noreferrer" target="_blank" href='https://github.com/Jsubcab/memory_game'>Matching cards</a></Card.Title>
                            <Card.Text>
                              Browser game created with Javascript
                            </Card.Text>
                          
                          </Card.Footer>
                      </Card>
                  </div>
                </Col>

                <Col sm={12} md={6} className='list-columns'>
                  <div className='list-projects'>
                    <Card >
                          <Card.Img variant="top" src={Jamming} />
                          <Card.Footer>
                              <p className='projects-section'>COURSE PROJECT</p>
                              <Card.Title><a rel="noopener noreferrer" target="_blank" href='https://github.com/Jsubcab/jammming'>Jamming</a></Card.Title>
                              <Card.Text>
                                Tool created with React for interact with Spotify's API and edit your playlists
                              </Card.Text>
                            </Card.Footer>
                        </Card>
                    </div>
                </Col>
              </Row>
        </section>  
    </div>
  );
  }
}

export default App;
